n    background: transparent;\n    box-shadow: none !important;\n\n    .card-header:not(.card-avatar) {\n        margin-left: 0;\n        margin-right: 0;\n\n        .card-category,\n        .card-description{\n          color: $gray-color;\n        }\n    }\n\n\n\n    .card-body {\n        padding-left: 5px;\n        padding-right: 5px;\n    }\n\n    .card-header-image {\n        margin: 0 !important;\n        border-radius: $border-radius-large;\n\n        img {\n          border-radius: $border-radius-large;\n        }\n    }\n    .card-footer {\n        padding-left: 5px;\n        padding-right: 5px;\n        background-color: transparent;\n    }\n}\n","%common-card-testimonial {\n    margin-top: 30px;\n    text-align: center;\n\n\n    .card-avatar {\n        margin: -50px auto 0;\n        border-radius: 50%;\n        overflow: hidden;\n        padding: 0;\n\n        @include shadow-big();\n\n        & + .card-body {\n            margin-top: 15px;\n        }\n        img {\n            width: 100%;\n            height: auto;\n        }\n    }\n\n    .card-body + .card-footer {\n        margin-top: -15px;\n    }\n\n    .card-footer {\n        .btn.btn-just-icon {\n            font-size: 20px;\n            padding: 12px 12px;\n            line-height: 1em;\n        }\n    }\n\n    &.card-plain {\n        .card-avatar {\n            margin-top: 0;\n        }\n    }\n}\n\n.card-testimonial {\n\n    @extend %common-card-testimonial;\n\n  .card-avatar {\n      max-width: 100px;\n      max-height: 100px;\n  }\n  .card-footer {\n      margin-top: 0;\n      display: block;\n\n      .card-avatar {\n          margin-top: 10px;\n          margin-bottom: -60px;\n      }\n  }\n  .card-description {\n      font-style: italic;\n\n      & + .card-title {\n          margin-top: 30px;\n      }\n  }\n  .icon {\n      margin-top: 30px;\n\n      .material-icons {\n          font-size: 40px;\n      }\n  }\n}\n",".card-profile{\n  @extend %common-card-testimonial;\n}\n\n.card-profile{\n  .card-header:not([class*=\"card-header-\"]){\n    background: transparent;\n    box-shadow: none;\n  }\n  .card-avatar {\n    max-width: 130px;\n    max-height: 130px;\n  }\n}\n",".card-blog{\n    margin-top: 60px;\n\n    [class*=\"col-\"]{\n        .card-header-image{\n            img{\n                width: 100%;\n            }\n        }\n    }\n    .carf-footer{\n        .stats{\n            .material-icons{\n                font-size: 18px;\n                position: relative;\n                top: 4px;\n                width: 19px;\n            }\n        }\n    }\n}\n",".card-product{\n    margin-top: 30px;\n\n    .btn-simple.btn-just-icon{\n        padding: 0;\n    }\n\n    .card-footer{\n        .price{\n            h4{\n                margin-bottom: 0;\n            }\n        }\n        .btn{\n            margin: 0;\n        }\n    }\n\n    .card-title,\n    .card-category,\n    .card-description{\n        text-align: center;\n    }\n    .category{\n        margin-bottom: 0;\n    }\n    .category ~ .card-title{\n        margin-top: 0;\n    }\n\n    .price{\n        font-size: 18px;\n        color: #9A9A9A;\n    }\n    .price-old{\n        text-decoration: line-through;\n        font-size: 16px;\n        color: #9A9A9A;\n    }\n    .price-new{\n        color: $brand-danger;\n    }\n}\n",".card-pricing{\n    &:after{\n        background-color: rgba(0, 0, 0, 0.7) !important;\n    }\n\n    text-align: center;\n\n    .card-title{\n        margin-top: 30px;\n    }\n    .card-body{\n        padding: 15px !important;\n        margin: 0 !important;\n    }\n\n    .card-icon{\n        padding: 10px 0 0px;\n\n        i{\n            font-size: 55px;\n            border: 1px solid #E5E5E5;\n            border-radius: 50%;\n            width: 130px;\n            line-height: 130px;\n            height: 130px;\n        }\n    }\n\n    h1{\n        small{\n            font-size: 18px;\n            display: inline-flex;\n            height: 0;\n\n            &:first-child{\n                position: relative;\n                top: -17px;\n                font-size: 26px;\n            }\n        }\n    }\n\n\n    ul{\n        list-style: none;\n        padding: 0;\n        max-width: 240px;\n        margin: 10px auto;\n\n        li{\n            color: $gray-color;\n            text-align: center;\n            padding: 12px 0;\n            border-bottom: 1px solid rgba($gray-color,.3);\n\n            &:last-child{\n                border: 0;\n            }\n            b{\n                color: $black-color;\n            }\n            i{\n                top: 6px;\n                position: relative;\n            }\n        }\n    }\n\n    &.card-background,\n    &[class*=\"bg-\"]{\n        ul{\n            li{\n                color: $white-color;\n                border-color: rgba($white-color,.3);\n\n                b{\n                    color: $white-color;\n                    font-weight: 700;\n                }\n            }\n        }\n        [class*=\"text-\"],\n        .card-category{\n            color: $white-color !important;\n        }\n    }\n\n    .card-footer{\n        z-index: 2;\n    }\n}\n",".card-collapse,\n.card-collapse .card-header{\n    box-shadow: none;\n    background-color: transparent;\n    border-radius: 0;\n}\n\n.card-collapse{\n    margin: 0;\n\n    & .card-header{\n        border-bottom: 1px solid #ddd;\n        padding: 25px 10px 5px 0px;\n        margin: 0;\n        box-shadow: none !important;\n        background: $white;\n\n        a{\n            color: $black-color;\n            font-size: 0.9375rem;\n            display: block;\n\n            &:hover,\n            &:active,\n            &[aria-expanded=\"true\"]{\n                color: $brand-rose;\n            }\n\n            i{\n                float: right;\n                top: 4px;\n                position: relative;\n            }\n\n            &[aria-expanded=\"true\"] i{\n                @include rotate-180();\n            }\n        }\n    }\n\n    .card-body{\n        padding: 15px 0px 5px;\n    }\n}\n","// User select\n// For selecting text on the page\n\n@mixin user-select($select) {\n  -webkit-user-select: $select;\n     -moz-user-select: $select;\n      -ms-user-select: $select; // IE10+\n          user-select: $select;\n}\n\n@mixin box-shadow($shadow...) {\n  -webkit-box-shadow: $shadow; // iOS <4.3 & Android <4.1\n          box-shadow: $shadow;\n}\n\n// Box sizing\n@mixin box-sizing($boxmodel) {\n  -webkit-box-sizing: $boxmodel;\n     -moz-box-sizing: $boxmodel;\n          box-sizing: $boxmodel;\n}\n\n\n@mixin transition-all($time, $type){\n    -webkit-transition: all $time $type;\n    -moz-transition: all $time $type;\n    -o-transition: all $time $type;\n    -ms-transition: all $time $type;\n    transition: all $time $type;\n}\n\n@mixin transform-scale($value){\n     -webkit-transform: scale($value);\n        -moz-transform: scale($value);\n        -o-transform: scale($value);\n        -ms-transform: scale($value);\n        transform: scale($value);\n}\n\n@mixin transform-translate-x($value){\n     -webkit-transform:  translate3d($value, 0, 0);\n        -moz-transform: translate3d($value, 0, 0);\n        -o-transform: translate3d($value, 0, 0);\n        -ms-transform: translate3d($value, 0, 0);\n        transform: translate3d($value, 0, 0);\n}\n\n@mixin transform-translate-y($value){\n     -webkit-transform:  translate3d(0,$value,0);\n        -moz-transform: translate3d(0,$value,0);\n        -o-transform: translate3d(0,$value,0);\n        -ms-transform: translate3d(0,$value,0);\n        transform: translate3d(0,$value,0);\n}\n\n@mixin transform-origin($coordinates){\n      -webkit-transform-origin: $coordinates;\n        -moz-transform-origin: $coordinates;\n        -o-transform-origin: $coordinates;\n        -ms-transform-origin: $coordinates;\n        transform-origin: $coordinates;\n}\n\n@mixin radial-gradient($extern-color, $center-color){\n    background: $extern-color;\n    background: -moz-radial-gradient(center, ellipse cover, $center-color 0%, $extern-color 100%); /* FF3.6+ */\n    background: -webkit-gradient(radial, center center, 0px, center center, 100%, color-stop(0%,$center-color), color-stop(100%,$extern-color)); /* Chrome,Safari4+ */\n    background: -webkit-radial-gradient(center, ellipse cover, $center-color 0%,$extern-color 100%); /* Chrome10+,Safari5.1+ */\n    background: -o-radial-gradient(center, ellipse cover, $center-color 0%,$extern-color 100%); /* Opera 12+ */\n    background: -ms-radial-gradient(center, ellipse cover, $center-color 0%,$extern-color 100%); /* IE10+ */\n    background: radial-gradient(ellipse at center, $center-color 0%,$extern-color 100%); /* W3C */\n    background-size: 550% 450%;\n}\n\n@mixin vertical-align {\n  position: relative;\n  top: 50%;\n  -webkit-transform: translateY(-50%);\n  -ms-transform: translateY(-50%);\n  transform: translateY(-50%);\n}\n\n@mixin rotate-180(){\n    filter: progid:DXImageTransform.Microsoft.BasicImage(rotation=2);\n    -webkit-transform: rotate(180deg);\n    -ms-transform: rotate(180deg);\n    transform: rotate(180deg);\n}\n\n@mixin bar-animation($type){\n     -webkit-animation: $type 500ms linear 0s;\n     -moz-animation: $type 500ms linear 0s;\n     animation: $type 500ms 0s;\n     -webkit-animation-fill-mode: forwards;\n     -moz-animation-fill-mode: forwards;\n     animation-fill-mode: forwards;\n}\n\n@mixin topbar-x-rotation(){\n    @keyframes topbar-x {\n      0% {top: 0px; transform: rotate(0deg); }\n      45% {top: 6px; transform: rotate(145deg); }\n      75% {transform: rotate(130deg); }\n      100% {transform: rotate(135deg); }\n    }\n    @-webkit-keyframes topbar-x {\n      0% {top: 0px; -webkit-transform: rotate(0deg); }\n      45% {top: 6px; -webkit-transform: rotate(145deg); }\n      75% {-webkit-transform: rotate(130deg); }\n      100% { -webkit-transform: rotate(135deg); }\n    }\n    @-moz-keyframes topbar-x {\n      0% {top: 0px; -moz-transform: rotate(0deg); }\n      45% {top: 6px; -moz-transform: rotate(145deg); }\n      75% {-moz-transform: rotate(130deg); }\n      100% { -moz-transform: rotate(135deg); }\n    }\n}\n\n@mixin topbar-back-rotation(){\n    @keyframes topbar-back {\n      0% { top: 6px; transform: rotate(135deg); }\n      45% { transform: rotate(-10deg); }\n      75% { transform: rotate(5deg); }\n      100% { top: 0px; transform: rotate(0); }\n    }\n\n    @-webkit-keyframes topbar-back {\n      0% { top: 6px; -webkit-transform: rotate(135deg); }\n      45% { -webkit-transform: rotate(-10deg); }\n      75% { -webkit-transform: rotate(5deg); }\n      100% { top: 0px; -webkit-transform: rotate(0); }\n    }\n\n    @-moz-keyframes topbar-back {\n      0% { top: 6px; -moz-transform: rotate(135deg); }\n      45% { -moz-transform: rotate(-10deg); }\n      75% { -moz-transform: rotate(5deg); }\n      100% { top: 0px; -moz-transform: rotate(0); }\n    }\n}\n\n@mixin bottombar-x-rotation(){\n    @keyframes bottombar-x {\n      0% {bottom: 0px; transform: rotate(0deg);}\n      45% {bottom: 6px; transform: rotate(-145deg);}\n      75% {transform: rotate(-130deg);}\n      100% {transform: rotate(-135deg);}\n    }\n    @-webkit-keyframes bottombar-x {\n      0% {bottom: 0px; -webkit-transform: rotate(0deg);}\n      45% {bottom: 6px; -webkit-transform: rotate(-145deg);}\n      75% {-webkit-transform: rotate(-130deg);}\n      100% {-webkit-transform: rotate(-135deg);}\n    }\n    @-moz-keyframes bottombar-x {\n      0% {bottom: 0px; -moz-transform: rotate(0deg);}\n      45% {bottom: 6px; -moz-transform: rotate(-145deg);}\n      75% {-moz-transform: rotate(-130deg);}\n      100% {-moz-transform: rotate(-135deg);}\n    }\n}\n\n@mixin bottombar-back-rotation{\n    @keyframes bottombar-back {\n      0% { bottom: 6px;transform: rotate(-135deg);}\n      45% { transform: rotate(10deg);}\n      75% { transform: rotate(-5deg);}\n      100% { bottom: 0px;transform: rotate(0);}\n    }\n    @-webkit-keyframes bottombar-back {\n      0% {bottom: 6px;-webkit-transform: rotate(-135deg);}\n      45% {-webkit-transform: rotate(10deg);}\n      75% {-webkit-transform: rotate(-5deg);}\n      100% {bottom: 0px;-webkit-transform: rotate(0);}\n    }\n    @-moz-keyframes bottombar-back {\n      0% {bottom: 6px;-moz-transform: rotate(-135deg);}\n      45% {-moz-transform: rotate(10deg);}\n      75% {-moz-transform: rotate(-5deg);}\n      100% {bottom: 0px;-moz-transform: rotate(0);}\n    }\n\n}\n\n@mixin timeline-badge-color($color) {\n    background-color: $color;\n    @include shadow-big-color($color);\n}\n\n\n@mixin lock-page-input-color($color) {\n    &.lock-page{\n        .form-group{\n            .form-control{\n                background-image: linear-gradient($color, $color), linear-gradient($mdb-input-underline-color, $mdb-input-underline-color);\n            }\n        }\n\n    }\n}\n",".card-signup{\n    .card-header{\n        // @include shadow-big();\n        margin-left: 20px;\n        margin-right: 20px;\n        margin-top: -40px;\n        padding: 20px 0;\n        width: 100%;\n        margin-bottom: 15px;\n    }\n    .text-divider{\n        margin-top: 30px;\n        margin-bottom: 0px;\n        text-align: center;\n    }\n    .card-body{\n        padding: 0px 30px 0px 10px;\n    }\n\n    .form-check{\n        // padding-top: 27px;\n\n        label{\n            margin-left: 18px;\n        }\n        .form-check-sign{\n            padding-right: 27px;\n            left: 27px;\n        }\n\n    }\n\n    .social-line{\n        margin-top: $bmd-label-top-margin-base;\n        text-align: center;\n        padding: 0;\n\n        .btn{\n            color: $white-color;\n            margin-left: 5px;\n            margin-right: 5px;\n        }\n    }\n}\n",".card-form-horizontal{\n    .card-body{\n        padding-left: 15px;\n        padding-right: 15px;\n    }\n    .form-group,\n    .input-group{\n        .form-control{\n            margin-bottom: 0;\n        }\n    }\n\n